import Vue from 'vue';
import jump from './Jump.vue';
import axios from '../../lib/axios.config';

Vue.prototype.$http = axios;
Vue.config.productionTip = false;

const FastClick = require('fastclick')
FastClick.attach(document.body);

new Vue({
  el: '#app',
  template: '<jump/>',
  components: { jump }
})


