import tools from '../../lib/tools.js';
export default {
    name:'jump',
    created(){
        let time = '?_'+ (new Date().getTime());
        let tempURL = tools.getRequestParam();
        if(tempURL.url){
            sessionStorage.setItem('tempURLtempURL',JSON.stringify(tempURL));
            localStorage.setItem('md',tempURL.managerId || tools.getCookie('user_id'));
            let jumpURL = window.location.href;
            jumpURL = jumpURL.slice(jumpURL.indexOf('url=')+4);
            tools.next(sessionStorage.getItem('tempURL') || jumpURL);
            // decodeURI(jumpURL.slice(0,jumpURL.indexOf('&')));
        }else{
            window.location.href = '/m/login.html'+time;
        }
    }
}



